document.querySelector("#icon-link").href = require("../images/favicon.ico");
import "../css/swiper.min.css";
import "../css/index.css";
import { data } from "../js/information.js";
import { Swiper } from "../js/swiper.min.js";
new Swiper(".mySwiper", {
    slidesPerView: 3,
    spaceBetween: 5,
    pagination: {
        el: ".swiper-pagination",
        clickable: true,
        dynamicBullets: true,
    },
});

// 判断是否支持HMR功能
if (module.hot) {
    module.hot.accept("../js/index.js");
    module.hot.accept("../js/index.js");
}
// PWA
if ("serviceWorker" in navigator) {
    window.addEventListener("load", () => {
        navigator.serviceWorker
            .register("/service-worker.js")
            .then(registration => {
                console.log("SW registered: ", registration);
            })
            .catch(registrationError => {
                console.log("SW registration failed: ", registrationError);
            });
    });
}

window.addEventListener("load", () => {
    // variable
    const headerHomeBtn = document.getElementById("home");
    const headerModelBtn = document.getElementById("model");
    const mainContainer = document.querySelector(".main");
    const listContainer = document.querySelector(".list_container");
    const textLine = document.querySelector(".text_line");
    const navListLink = document.querySelectorAll(".nav_list");
    const navListLine = document.querySelector(".nav>.nav_list_line");
    const listsBox = document.querySelector(".lists_box");
    let seriesArr = [];
    let paramValue;
    let clickedLink = null;
    let animationOpacity = 1;
    const headerContact = document.querySelector(".header_contact");
    const contactBox = document.querySelector(".contact_box");
    const contactClose = document.querySelector(".contact_close");
    const avatarBox = document.querySelector(".avatar_box");
    const titleDownBox = document.querySelector(".title_down_box");
    const startContainer = document.querySelector(".start_container");

    //  animation background
    let animation1 = setTimeout(() => {
        let animationTimer = setInterval(() => {
            animationOpacity -= 0.1;
            startContainer.style.opacity = animationOpacity;
            if (animationOpacity <= 0) {
                startContainer.remove();
                clearTimeout(animationTimer);
            }
        }, 100);
        clearTimeout(animation1);
    }, 4000);

    // click contact icon
    headerContact.addEventListener("click", () => {
        if (contactBox.getAttribute("data-close") == "true") {
            fadeOutAndIn(contactBox, 0, "flex", 100);
            contactBox.setAttribute("data-close", "false");
        }
    });
    contactClose.addEventListener("click", () => {
        fadeOutAndIn(contactBox, 1, "none", 100);
        contactBox.setAttribute("data-close", "true");
    });
    avatarBox.addEventListener("click", () => {
        if (titleDownBox.getAttribute("data-isDown") == "false") {
            titleDownBox.style.height = "68%";
            titleDownBox.setAttribute("data-isDown", "true");
            contactClose.style.color = "white";
            contactClose.style.border = " 0.1333vw solid white";
        } else {
            titleDownBox.style.height = "0";
            titleDownBox.setAttribute("data-isDown", "false");
            contactClose.style.color = "#ff8989";
            contactClose.style.border = " 0.1333vw solid #ff8989";
        }
    });

    // click the title button
    headerHomeBtn.addEventListener("click", e => {
        e.stopPropagation();
        e.preventDefault();
        clickHeaderBtn("1.8667vw", "text_active", "block", "none", "0", true);
    });
    headerModelBtn.addEventListener("click", e => {
        e.stopPropagation();
        e.preventDefault();
        renderList("jx");
        clickHeaderBtn("12.69vw", "text_active", "none", "block", "1");
        navListLine.style.left = 6 + "vw";
    });

    // nav list click and push list_card
    navListLink.forEach((li, index) => {
        li.addEventListener("click", function handleClick(e) {
            e.stopPropagation();
            // e.preventDefault();
            // index * li宽度20vw + (li宽度20vw - 下划线宽度8vw) / 2   除以2是为了算出两边留下的宽度
            navListLine.style.left = index * 20 + (20 - 8) / 2 + "vw";
            clickHeaderBtn("12.69vw", "text_active", "none", "block", "1");
            if (e.target !== clickedLink) {
                listsBox.innerHtml = "";
                paramValue = li.getAttribute("data-param");
                renderList(paramValue);
                clickedLink = e.target;
                e.target.removeEventListener("click", handleClick);
                setTimeout(() => {
                    e.target.addEventListener("click", handleClick);
                }, 100);
            }
        });
    });
    function renderList(value) {
        data.forEach(e => {
            if (e.name == value) {
                let newData = e.mainInfo
                    .map(m => {
                        return ` <div class="list_card">
              <a href="${m.url}" class="list_img">
                  <img src="${m.src}" alt="" />
              </a>
              <div class="list_title">
                  <span>${m.title1}</span>
                  <span>${m.style}</span>
              </div>
            </div>`;
                    })
                    .join("");
                listsBox.innerHTML = newData;
                seriesArr += listsBox.innerHTML;
            }
        });
        listsBox.innerHTML = seriesArr;
        seriesArr = [];
    }

    // carouselMap

    // go_back
    const goBack = document.getElementById("go_back");
    goBack.addEventListener("click", () => {
        window.scrollTo({ top: 0, behavior: "smooth" });
    });
    function throttle(func, wait) {
        // 节流函数
        let prev = 0;
        return function (...args) {
            const now = Date.now();
            if (now - prev > wait) {
                func(...args);
                prev = now;
            }
        };
    }
    function scrollCallback() {
        const scrollTop = document.documentElement.scrollTop;
        // goBack.style.display = "block";
        if (scrollTop > 500) {
            goBack.style.opacity = 1;
        } else {
            goBack.style.opacity = 0;
        }
    }
    window.addEventListener("scroll", throttle(scrollCallback, 300));

    // function
    /**
     *
     * @param {string} textLineLeft
     * @param {string} textActive
     * @param {string} active1
     * @param {string} active2
     * @param {number} opacity
     * @param {boolean} isToggle
     */
    function clickHeaderBtn(textLineLeft, textActive, active1, active2, opacity, isToggle) {
        textLine.style.left = textLineLeft;
        if (isToggle) {
            headerHomeBtn.classList.add(textActive);
            headerModelBtn.classList.remove(textActive);
        } else {
            headerHomeBtn.classList.remove(textActive);
            headerModelBtn.classList.add(textActive);
        }
        mainContainer.style.display = active1;
        listContainer.style.display = active2;
        navListLine.style.opacity = opacity;
    }

    // block and none function
    /**
     *
     * @param {target} element
     * @param {opacity} transparency
     * @param {display} mode
     * @param {number} speed
     */
    function fadeOutAndIn(element, transparency, mode, speed) {
        let opacity = transparency;
        if (transparency === 0) {
            element.style.display = mode;
        }
        let timer = setInterval(() => {
            if (transparency === 0) {
                opacity += 0.2;
                element.style.opacity = opacity;
                if (opacity >= 1) {
                    clearInterval(timer);
                }
            }
            if (transparency === 1) {
                opacity -= 0.1;
                element.style.opacity = opacity;
                if (opacity <= 0) {
                    clearInterval(timer);
                    element.removeAttribute("style");
                }
            }
        }, speed);
    }
});
